@import '../../theme/variables.scss';

:local .header{
  text-shadow: rgb(0, 0, 0) 1px 0px 4px, rgb(85, 85, 85) -1px -1px 4px;
  background: no-repeat center center;
  background-size: cover;
  color: #fff;
  min-height: 14em;

  :global(h1){
    font-family: $font_sofia_light;
    color: #fff;
    padding-top: 2em;
    padding-bottom: 2em;
    font-size: 3.5em;

    :global(small) {
      font-size: 0.45em;
      color: #fff;
    }
  }
}

:local .groups{
  :local .columnContent{
    height: 250px;
    display: block;
  }
}



