@import '../../theme/variables.scss';

:local .container{
  width: 100%;
  height: 275px;
  display: block;
  border: 1px solid $peek_gray;
  background-size: cover;

  &:hover{
    box-shadow: 0px 0px 10px rgba(0,0,0,0.5);
  }
}

:local .content{
  position: absolute;
  bottom: 0px;
  left: 0px;
  right: 0px;
  color: #fff;
  text-shadow: rgb(0, 0, 0) 1px 0px 4px, rgb(85, 85, 85) -1px -1px 4px;

  :local .background{
    background: linear-gradient(to bottom, transparent 0%, rgba(0,0,0,0.8) 100%);
    padding: 1em;
  }
}

:local .tagPageImages{
  width: 100%;
  height: 100%;
  padding-bottom: 40px;

  :local .halfImage{
    width: 50%;
    height: 100%;
    display: inline-block;
    padding-left: 4px;
  }

  :local .stackedImage{
    width: 100%;
    height: calc(50% - 2px);
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    &:first-child{
      margin-bottom: 3px;
    }
    &:last-child{
      margin-top: 3px;
    }
  }
}



