@import '../../../theme/variables.scss';

:local .popover{
  width: auto;
  font-family: $font_sofia_light;
  max-width: 500px;
  min-width: 300px;
  line-height: 175%;
  padding-bottom: 1.5em;
}

:local .breadCrumb{
  // This is to offset the breadcrumb
  margin-left: -15px;

  :global(i){
    display: inline-block;
    vertical-align: top;
    padding-right: 10px;
    color: $text-color !important;
  }
}



